.repair-services-box {
    @media screen and (min-width: 1200px) {
    }
}

.service-cost {
    padding-top: 21px;
    padding-bottom: 11px;
    margin-left: -20px;
    margin-right: -20px;
    margin-top: 20px;

    box-shadow: 0px 0px 10px rgba(0, 37, 74, 0.2);
    border-radius: 4px;
    
    &--position-absolute {
        @media screen and (min-width: 1200px) {
            position: absolute;
            top: 0;
            right: -332px;
        }
    }

    @media screen and (min-width: 1200px) {
        width: 300px;
        height: 294px;

        padding-top: 33px;
        padding-bottom: 27px;
        margin: 0;
    }
}

.service-cost__title {
    margin-bottom: 11px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.18;
    text-align: center;

    color: var(--primary-text-color);

    @media screen and (min-width: 1200px) {
        margin-bottom: 32px;

        font-size: 24px;
        line-height: 1.16;
    }
}

.service-cost__text-number {
    margin-bottom: 15px;

    font-weight: 400;
    font-size: 34px;
    line-height: 1.17;
    text-align: center;

    color: var(--primary-text-color);

    @media screen and (min-width: 1200px) {
        margin-bottom: 41px;

        font-size: 45px;
        line-height: 1.17;
    }
}

.service-cost__text-currency {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
}

.service-cost__button {
    display: block;
    width: 211px;
    height: 40px;
    @extend %marginLR-auto;
    margin-bottom: 19px;

    font-weight: 700;
    font-size: 14px;
    line-height: 1.14px;
    text-transform: uppercase;

    border: none;
    border-radius: 4px;
    color: var(--white-color);
    background-color: var(--accent-color);
    cursor: pointer;

    transition: background-color 250ms;

    &:hover {
        background-color: #0060AA;
    }
}

.service-cost__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 1.6;
    text-align: center;
}